/***********************************************************************
*  Grid Panel
**********************************************************************/
.grid-wrapper {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(350px, 1fr));
  grid-gap: 20px;
}

.box {
  background-color: #444;
  padding: 130px;
  margin: 20px;
}

.box > img {
  width: 100%;
}

.box:hover {
  -webkit-transform: rotate(-7deg);
  -moz-transform: rotate(-7deg);
  -o-transform: rotate(-7deg);
  transform: rotate(-7deg);
}

.box > img {
  width: 100%;
  height: auto;
}

/***********************************************************************
*  Footer
**********************************************************************/
footer {
  text-align: center;
  background-color: #444;
}

